.city-list-header-ctn {
  display: flex;
  align-items: center;
  font-size: 24px;
  margin-bottom: 16px;
}

.city-list {
  display: flex;
  flex-wrap: wrap;
}

.city-card {
  padding: 0 12px;
  width: calc(33.3% - 16px);
  max-width: 500px;
  min-width: 260px;
  display: flex;
  align-items: center;

  border: 1px solid #dadce0;
  border-radius: 8px;
  height: 100px;
  margin: 0 16px 16px 0;
  position: relative;
  background-color: white;
}

.city-card:hover {
  transition: box-shadow 0.28s ease-in-out;
  border: 1px solid #dadce0;
  box-shadow: 0 1px 3px 0 rgba(60, 64, 67, 0.05),
    0 4px 8px 3px rgba(60, 64, 67, 0.15);
  cursor: pointer;
}
.city-card:hover .city-remove {
  display: inline;
}

.city-card-content {
  flex: 1;
  display: flex;
  align-items: center;
  height: 32px;
}

.city-remove {
  opacity: 0.5;
  width: 24px;
  cursor: pointer;
  border-radius: 50%;
  display: none;
  position: absolute;
  top: 4px;
  right: 4px;
}

.city-remove:hover {
  opacity: 1;
  background-color: #f1f3f4;
}

.city-weather-icon {
  opacity: 0.7;
  margin: 0 8px;
}
