.local-time {
  opacity: 0.6;
  font-size: 14px;
  font-weight: 500;
  margin-top: 6px;
}

.detail-header-ctn {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.detail-icon-ctn {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 8px;
}

.detail-action-icon {
  cursor: pointer;
  width: 24px;
  margin-left: 8px;
}

.text-area {
  border: #00000052 1px solid;
  border-radius: 4px;
}
.button {
  background-color: #4184f3;
  border-radius: 5px;
  color: white;
  border: none;
  cursor: pointer;
  height: 28px;
  margin-left: 16px;
  width: 68px;
}

.city-detail-temp {
  font-size: 84px;
  font-weight: 500;
  line-height: 0.8;
  position: relative;
  top: -24px;
}
.city-detail-feellike {
  opacity: 0.6;
  position: absolute;
  bottom: 17px;
}

.city-detail-rows-ctn {
  column-count: 2;
  margin-top: 24px;
}

.city-detail-row {
  display: flex;
  justify-content: space-between;
  max-width: 320px;
  padding: 8px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.15);
  break-inside: avoid;
}
