.header-ctn {
  display: flex;
  width: 100vw;
  height: 62px;
  position: fixed;
  top: 0;
  box-shadow: 0 1px 2px 0 rgba(60, 64, 67, 0.3),
    0 2px 6px 2px rgba(60, 64, 67, 0.15);
  background-color: white;
  justify-content: center;
  align-items: center;
  z-index: 2;
}
.header-content {
  width: 100%;
  height: 100%;
  max-width: 1024px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-menu {
  color: #5f6368;
  height: 100%;
  display: flex;
  align-items: center;
}

.header-active {
  border-bottom: 3px solid #4285f4;
  color: #202124;
}
